ï»¿.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}

.footer-info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
}

.footer-nav{
    display: flex;
    flex-direction: column;
    margin-right: 100px;
}

.box{
    border: 1px solid black;
}

.horizontal{
    display: flex;
    justify-content: space-around;
    align-items: stretch;
}

.menu {
    display: flex;
}

.menu ul {
    list-style: none;
    display: flex;
    flex-direction: row;
}

.menu ul li {
    margin-right: 20px;
}

.menu-toggle{
    display: none;
}

.feature{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.hero-buttons{
    display: flex;
    flex-direction: column;
}

.services-grid{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.service-card{
    border: 1px solid black;
    margin: 20px;
}

.achievements-grid{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.achievement-box{
    border: 1px solid black;
    margin: 20px;
}

.about-image{
    max-width: 20%;
    height: auto;
}

.delimiter{
    display: block;
    height: 120px;
    border: 1px solid black;
}

.contact-card{
    flex: 1;
    border: 1px solid black;
}

.map{
    display: block;
    border: 1px solid black;
    flex: 1;
}

@media (max-width: 768px){
    .menu{
        display: none;
    }

    .menu-active .menu{
        display: block;
    }

    .menu-toggle{
        display: block;
    }

    .header-logo{
        order: 2;
    }

    .header-nav{
        order: 3;
    }

    .header-contacts{
        order: 1;
    }

    .feature{
        flex-direction: column;
    }

    .footer-info{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
    }

    .footer-nav{
        display: none;
    }

    .services-grid{
        display: grid;
        grid-template-columns: 1fr;
    }

    .achievements-grid{
        display: grid;
        grid-template-columns: 1fr;
    }

    .about-image{
        display: none;
    }

    .delimiter{
        display: block;
        height: 65px;
        border: 1px solid black;
    }
}